import mapUtil from '../../components/mapUtil/map_tool.js'
export default {
	data() {
		return {
			center: {lng: 109.45744048529967, lat: 36.49771311230842},
			zoom: 13,
			pointList:[
				{lng:'108.49926175379778',
				 lat:'36.60449676862417',
				 name:'张三',
				 uid:'1'
				},
				{lng:'107.59926175379778',
				 lat:'36.40449676862417',
				 name:'李四',
				 uid:'2'
				},
				{lng:'109.79926175379778',
				 lat:'36.50449676862417',
				 name:'王五',
				 uid:'3'
				}
			]
		};
	},
	mounted() {
	},
	onLoad() {
		var today = new Date();
		this.date = today.getFullYear()+"-"+(today.getMonth()+1)+"-"+today.getDate();
		uni.setNavigationBarColor({
		    frontColor: '#000000',
		    backgroundColor: '#ffffff'
		})
		uni.setNavigationBarTitle({
		    title: '人员轨迹'
		});
		//判断用户是否已经登录过
		var userInfo = uni.getStorageSync('userInfo');
		if(!userInfo){
			uni.navigateTo({
				url: '../../pages/login/login'
			});
		}
	},
	methods:{
		handler ({BMap, map}) {
			mapUtil.map_tool.init({
				"map": map,
				"datas": this.pointList,
				"base_path": ""
			});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		RadioChange(e) {
			this.radio = e.detail.value
			//this.name = e.detail
			//this.personnelName = e.detail.value;
			this.personnelName = this.personnelList[e.detail.value].name;
			this.personnelId = this.personnelList[e.detail.value].id;
			
		},
		DateChange(e) {
			this.date = e.detail.value
		}
	}
}
